body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(to top right, #3775dd, #dc97dc);
  max-width: 1440px;
  margin: 0 auto;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Verdana, sans-serif;
  color: #000;
}

.App {
  text-align: center;
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  max-width: 50%;
  height: auto;
  background-color: rgba(236, 231, 231, 0.6);
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(101, 101, 101, 0.5);
}

@media screen and (max-width: 425px) {
  .App{
    max-width: 90%;
  }
}

h1 {
  margin: 0;
  padding: 20px 0 20px;
}


form {
    padding: 10px;
}

.inputs-field,
.txtAreas-field {
    border: none;
    margin: 0;
    padding: 0;
}

.bttns-field {
    display: flex;
    justify-content: space-around;
}

/* Input & TextAreas & Bttns common styles */

.input-field__label,
.txtArea-field__label {
    text-align: left;
    margin: 0 0 0.25rem 0.75rem;
} 

.input-field__label,
.input-field__inpt {
    display: block;
}

.input-field__inpt,
.txtArea-field__txtArea {
    padding: 0.375rem 0.75rem;
    color: #212529;
    background-color: #fff;
    border: 1px solid #bdbdbd;
    border-radius: 10px;
}

.input-field__inpt,
.txtArea-field__txtArea,
.cancel,
.save {
    font-family: inherit;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
}

@media screen and (max-width: 768px) {
    .input-field__inpt,
    .txtArea-field__txtArea,
    .cancel,
    .save {
        font-size: 0.65rem;
    }
}
.input-field {
    display: inline-block;
    margin-bottom: 1rem;
    width: 45%;
}

.input-field:nth-child(1),
.input-field:nth-child(3) {
    margin-right: 20px;
}

@media screen and (max-width: 768px) {
    .input-field:nth-child(1),
    .input-field:nth-child(3) {
        margin-right: 10px;
    }

    .input-field:nth-child(5) {
        width: 90%;
    }

    .input-field__inpt{
        font-size: 0.65rem;
    }
}

.input-field__inpt {
    width: 100%;
    height: calc(2.25rem + 2px);
}
.txtArea-field {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 1rem;
}

.txtArea-field__txtArea {
    resize: none;
}
.cancel,
.save {
    width: 30%;
    height: 50px;
    cursor: pointer ;
    background-color: #3775dd;
    box-shadow: 0 1px 2px 0 #21487f,  0 1px 3px 1px #21487f;
    border: none;
    border-radius: 10px;
}

@media screen and (max-width: 320px) {
    .cancel,
    .save {
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
}

.cancel:hover,
.save:hover {
    background-color: #297be6;
}

.cancel:active,
.save:active {
	background-color: #1a73e8;
}

